:local(.appHeader) {
  height: 48px;
  border-bottom: 1px solid var(--spectrum-global-color-gray-200);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  background-color: var(--spectrum-global-color-gray-50);
  top: 0px;
  right: 0px;
  left: 0px;
  padding-left: 8px;
  z-index: 10; }
  @media screen and (min-width: 961px) {
    :local(.appHeader) {
      display: none; } }

:local(.overlay) {
  background-color: var(--spectrum-alias-background-color-modal-overlay);
  opacity: 0.5;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 11; }
  :local(.overlay):local(.menuClosed) {
    display: none; }

:local(.sideBar) {
  z-index: 12;
  position: fixed;
  left: 0px;
  top: 0px;
  bottom: 0px;
  width: 248px; }
  @media screen and (max-width: 960px) {
    :local(.sideBar) {
      transform: translate3d(-248px, 0, 0);
      transition: transform 100ms ease-in-out; }
      :local(.sideBar):local(.menuOpen) {
        transform: translate3d(0, 0, 0); } }

:local(.spectrumSidebar) {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--spectrum-global-color-gray-75); }

:local(.header) {
  padding: 30px 24px 24px 24px;
  width: 248px; }

:local(.navigation) {
  padding: 0px 24px;
  flex: 1;
  flex-grow: 1;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto; }

:local(.logo) {
  width: 30px;
  display: block;
  margin-right: 16px; }

:local(.title) {
  display: flex;
  flex-direction: row; }

:local(.headerLink) {
  text-decoration-line: none !important; }

:local(.titleText) {
  margin: 0px !important; }

:local(.internal) {
  font-size: 12px;
  margin: 0px;
  color: #8E8E8E;
  font-weight: 600; }
