:local(.title) {
  display: flex;
  flex-direction: row;
  align-items: center; }

:local(.headerLink) {
  text-decoration-line: none !important;
  display: block; }
  :local(.headerLink):focus {
    outline: none; }
    :local(.headerLink):focus > :local(.logoContainer) {
      box-shadow: 0 0 0 2px #2680eb; }

:local(.logoContainer) {
  outline: none;
  padding: 1px 2px 2px 2px;
  border-radius: 4px;
  margin-top: -2px; }

:local(.titleText) {
  margin: 0px !important;
  font-size: 20px !important;
  align-items: center; }
  @media screen and (max-width: 768px) {
    :local(.titleText) {
      font-size: 24px !important; } }

:local(.logo) {
  width: 36px;
  height: 32px;
  display: block;
  margin-right: 16px;
  padding-top: 1px; }
  @media screen and (max-width: 768px) {
    :local(.logo) {
      width: 45px;
      height: 40px; } }

:local(.internal) {
  font-size: 11px;
  margin: 0px;
  color: #6E6E6E;
  letter-spacing: 0.06em; }
  @media screen and (max-width: 768px) {
    :local(.internal) {
      font-size: 13px !important; } }
