* {
  box-sizing: border-box; }

body {
  overflow: auto !important; }

:global(.flexContainer) {
  padding-left: 248px; }
  @media screen and (max-width: 960px) {
    :global(.flexContainer) {
      padding-left: 0px; } }
  :global(.flexContainer) :global(.site-mainContainer) {
    padding: 40px 52px 0px 52px;
    max-width: 1080px; }
    @media screen and (max-width: 768px) {
      :global(.flexContainer) :global(.site-mainContainer) {
        padding-left: 16px;
        padding-right: 16px; } }
    @media screen and (max-width: 960px) {
      :global(.flexContainer) :global(.site-mainContainer) {
        padding-top: 80px; } }

:global(.noMargin) {
  margin: 0px; }

:local(.pageHeading) {
  size: 65px; }

:global(.spectrum-Table-row) {
  cursor: auto; }
  :global(.spectrum-Table-row):hover {
    background-color: inherit !important;
    cursor: auto; }

p:global(.spectrum-Body3) {
  margin-bottom: 24px;
  margin-top: 0px; }

:global(.spectrum-Body3) {
  margin-bottom: 8px; }

:global(.well-box) {
  background-color: var(--spectrum-global-color-gray-75);
  border: 1px solid var(--spectrum-global-color-gray-200);
  padding: 15px 16px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 0px;
  margin-bottom: 24px;
  font-size: 16px; }

:global(.spectrum) {
  background-color: var(--spectrum-global-color-gray-50) !important; }

:global(.footerContainer) {
  padding-top: 0px !important;
  padding-bottom: 20px;
  margin-top: 0px; }

:global(.Footernav-region) {
  visibility: hidden  !important;
  height: 0px;
  overflow: hidden;
  padding: 0px;
  margin: 0px; }

:global(.footerContainer) a:hover {
  text-decoration: underline !important; }

:global(.Footernav-misc) {
  padding: 8px !important; }

:global(.site-footer-col) {
  padding: 0px 8px !important; }

:global(.switcherContainer) {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap; }
  :global(.switcherContainer) > * {
    margin-right: 37px; }
