:local(.width) img {
  display: block; }

:local(.responsiveImage) img {
  display: block; }

:local(.Anatomy) {
  display: flex;
  align-items: center; }

:local(.hasMobile) :local(.mobile) {
  display: none; }
  @media screen and (max-width: 768px) {
    :local(.hasMobile) :local(.mobile) {
      display: block; } }

:local(.hasMobile) :local(.desktop) {
  display: block; }
  @media screen and (max-width: 768px) {
    :local(.hasMobile) :local(.desktop) {
      display: none; } }
